#header{
    width: 100%;
    height: 80vh;
    margin-top: 70px;
}
#header h1{
    margin: 0 0 10px 0;
    font-size: 48px;
    font-weight: 700;
    color: black;
}
#header h2{
    color: #484848;
    font-size: 24px;
}

#header .btn-get-started{

    font-weight: 500;
    font-size: 16px;
    display: inline-block;
    padding: 10px 30px;
   border-radius: 30px;
    transition: 0.5s;
    color:#3498db;
    border:2px solid #3498db;
    text-decoration: none;
}
#header .btn-get-started:hover{
    background: #3498db;
    color:#fff;
}
#header-img{
    text-align: right;
}
#header .img-fluid{
    height: 400px;
    width: 400px;
    margin-right:40px;
}


#header .animated{
    animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

@-webkit-keyframes up-down{
    0%{
        transform: translateY(10px);
    }
    100%{
        transform: translateY(-10px);
    }
}
@keyframes up-down{
    0%{
        transform: translateY(10px);
    }
    100%{
        transform: translateY(-10px);
    }

}